.header__toggle {
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  color: inherit;
  font: inherit;
  background-color: transparent;
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #0ad9c6; }
  @media (max-width: 1023px) {
    .header__toggle {
      display: block; } }

.header__toggle-line {
  position: absolute;
  top: 23px;
  right: 12px;
  width: 23px;
  height: 1px;
  font-size: 0;
  background-color: #000000; }
  .header__toggle-line::after, .header__toggle-line::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 23px;
    height: 1px;
    background-color: #000000;
    transform-origin: 50% 50%;
    transition: top 0.3s 0.3s, transform 0.3s; }
  .header__toggle-line::after {
    top: 7px; }
  .header__toggle-line::before {
    top: -7px; }

